



















































































import { Component, Vue } from 'vue-property-decorator'
import { getHostList, addHost, putHost, delHost, stopHost, restartHost, getStorehouseList } from '@/api/admin'
import { IArticleData } from '@/api/types'
import { Form } from 'element-ui'

@Component({
  name: 'host'
})
export default class extends Vue {
  private list: IArticleData[] = []
  private listLoading = true
  private dialogFormVisible = false
  private title = '新增主机'
  private total = 0
  private sub = ''
  private storehouseList =[]
  private listQuery = {
    page: 1,
    pagesize: 10,
    housename: ''
  }
  private initForm = {
    hostcode: '',
    hostname: '',
    houseid: '',
    housecode: '',
    housename: '',
    hoststatus: '1',
    hostmodel: '',
    simcode: ''
  }
  private form = { ...this.initForm }
  private formLabelWidth = '120px'
  private rules = {
    hostcode: [
      { required: true, message: '请输入主机编号', trigger: 'blur' }
    ],
    hostname: [
      { required: true, message: '请输入主机名称', trigger: 'blur' }
    ],
    houseid: [
      { required: true, message: '请输入所属仓库id', trigger: 'blur' }
    ]
  }
  created() {
    this.getList()
    this.getStorehouseList()
  }
  // 列表数据获取
  private async getList() {
    this.listLoading = true
    const { data } = await getHostList(this.listQuery)
    this.list = data.items
    this.total = data.total
    this.listLoading = false
  }
  // 获取库房列表
  private async getStorehouseList() {
    const { data } = await getStorehouseList(this.listQuery)
    // console.log(data.items)
    this.storehouseList = data.items
  }
  //选择仓库时，赋值隐藏域
  private houseChange(data: any) {
    //console.log(item);
    this.form.housecode = data.housecode;
    this.form.housename = data.housename;
  }

  // 新增 修改
  private async changeHandel(row:any) {
    this.dialogFormVisible = true
    if (row) {
      this.title = '修改主机'
      this.sub = 'put'
      this.form = row
      console.log(row)
    } else {
      this.title = '新增主机'
      this.sub = 'add'
      this.form = this.initForm
    }
  }
  private async submitHandle() {
    (this.$refs.form as Form).validate(async(valid) => {
      if (valid) {
        if (this.sub === 'add') {
          const res = await addHost(this.form)
          if (res.status === 200) {
            this.$message({
              type: 'success',
              message: '添加成功!'
            })
            this.getList()
          }
        } else {
          const res = await putHost(this.form)
          if (res.status === 200) {
            this.$message({
              type: 'success',
              message: '修改成功!'
            })
            this.getList()
          }
        }
        this.dialogFormVisible = false
      }
    })
  }
  // 停用主机
  private async stopHandel(id:any) {
    this.$confirm('确定要停用该主机？', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      const res = stopHost({ id })
      if (res) {
        this.getList()
        this.$message({
          type: 'success',
          message: '主机已停用!'
        })
      }
    }).catch(() => {
      this.$message({
        type: 'info',
        message: '主机停用失败'
      })
    })
  }
  // 重启主机
  private async restartHandel(id:any) {
    this.$confirm('确定要重启该主机？', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      const res = restartHost({ id })
      if (res) {
        this.getList()
        this.$message({
          type: 'success',
          message: '主机重启中...!'
        })
      }
    }).catch(() => {
      this.$message({
        type: 'info',
        message: '主机重启失败！'
      })
    })
  }
  // 删除
  private async delHandel(id:any) {
    this.$confirm('确定要删除该主机？', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      const res = delHost({ id })
      if (res) {
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        this.getList()
      }
    }).catch(() => {
      this.$message({
        type: 'info',
        message: '已取消删除'
      })
    })
  }
  // 分页切换
  private async handleCurrentChange(page:any) {
    this.listQuery.page = page
    this.getList()
  }
}
